<template>
<!---->
  <section>
    <span></span>
  <input type="text" placeholder="" class="put" v-model="name">
    <hr>
    <div class="bon">
      <p class="sp pull-left" @click="chooseSex(1)" :class="{choosed:sex==1}"></p>
      <span class="pull-left"></span>
      <p class="sp pull-left" @click="chooseSex(2)" :class="{choosed:sex==2}"></p>
      <span class="pull-left"></span>
    </div>
    <div>
      <span></span>
      <input type="text" name="phone" placeholder="" class="put" v-model="phone">
    </div>
   <div class="box">
     <span class="pull-left"></span>
     <div class="pull-left box1">
     <router-link :to="{name:'searchAddress'}" tag="div" class="put">{{print}}</router-link>
     <input type="text" name="address_detail" placeholder="" v-model="address_detail" class="put">
     </div>
   </div>
    <div>
      <span></span>
      <input type="text" name="tag" placeholder="///" v-model="tag" class="put">
    </div>
    <div class="btn" @click="addAddress"></div>
  </section>
</template>

<script>
  import { Toast } from 'mint-ui';
    export default {
        name: "addAddress",
      data(){
          return{
            name:'',// 
            phone:'',// 
             sex:1, //
            address_detail:'',// 
             tag:'',//
            tag_type: 1, //
          }
      },
      computed:{
          print(){
            if (this.$store.state.zhang.site){
              return this.$store.state.zhang.site
            } else {
              return '//'
            }
          }
      },
      created(){
        this.$store.commit('changecity1',{name1:'',bull:false})
      },
      methods:{
        chooseSex(sex){
          this.sex=sex
        },
        addAddress(){
          if (this.name===''){
            Toast('')
          }else if (this.phone===''){
            Toast('')
          }else if (this.address_detail===''){
            Toast('')
          }
          if (this.tag == '') {
            this.tag_type = 2;
          }else if(this.tag == ''){
            this.tag_type = 3;
          }else if(this.tag == ''){
            this.tag_type = 4;
          }
        const api='https://elm.cangdu.org/v1/users/'+this.$store.state.zhang.userId+'/addresses'
          this.$http({method:'post',url:api,data:{
              address:this.$store.state.zhang.site,
              name:this.name,
              phone:this.phone,
              sex:this.sex,
              address_detail:this. address_detail,
              tag:this.tag,
              tag_type:this.tag_type,
              geohash:this.$store.state.zhang.geohash,
            }}).then((response)=>{
              console.log(response.data)
            this.$router.push({name:'chooseAddress'})
            })
        }
      },
    }
</script>

<style scoped>
.put{
  height: 0.2rem;
  margin-top: 0.2rem;
}
  .sp{
    width: 0.2rem;
    height: 0.2rem;
    border-radius: 50%;
    margin-left: .25rem;
    padding-left: 0.05rem;
    padding-top: 0.02rem;
  }
.choosed{
  background-color:#4cd964 ;
}
  .box{
    margin-top: 0.3rem;
    height: 0.8rem;
  }
  .box1{
    margin-left: 0.2rem;
    margin-top: -0.2rem;
  }
  .btn{
    width: 80%;
    height: 0.3rem;
    margin-top: 0.1rem;
    background-color: greenyellow;
    margin-left: 10%;
  }
  .bon{
    width: 100%;
    height: 0.4rem;
    border-bottom: 0.01rem solid lightgrey;
  }
</style>



