<template>
    <div class="rootgood">
      <h2></h2>
      <hr>
      <div class="supervision">
        <i class="iconfont">&#xe62b;</i>
        <div>
          <p>: </p>
          <p>:</p>
        </div>
      </div>
      <h2></h2>
      <hr>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
      <h3></h3>
      <p></p>
      <p></p>
      <p></p>
      <p></p>
    </div>
</template>

<script>
    export default {
        name: "good",
        beforeRouteEnter(to,from,next){
          next(
            vm=>{
              vm.$store.commit('changecity1',{name1:'',bull:''})
              vm.$store.commit('isfoot', false)
            }
          )
        },
       beforeRouteLeave(to,from,next){
           this.$store.commit('changecity1',{name1:'',bull:''})
           next()

      }
    }
</script>

<style scoped>
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.5rem  !important;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    color: red;
  }
  .supervision{
    display: flex;
  }
  .supervision>i{
    position: relative;
    top: 0.5rem;
  }
  .supervision>div{
    margin-left: 0.2rem;
  }
  p{
    margin: 0.2rem;
  }
  .rootgood{
    margin-left: 0.1rem;
  }
</style>



